import React, { Component } from 'react'
import './App.scss'
import { Route, Link } from 'react-router-dom'

import AuthenticatedRoute from './auth/components/AuthenticatedRoute'
import Header from './header/Header'
import SignUp from './auth/components/SignUp'
import SignIn from './auth/components/SignIn'
import SignOut from './auth/components/SignOut'
import ChangePassword from './auth/components/ChangePassword'
import Home from './Home'
import ProfileForm from './auth/components/ProfileForm'
import ProfileCreate from './auth/components/ProfileCreate'


class App extends Component {
  constructor () {
    super()

    this.state = {
      user: null,
      flashMessage: '',
      flashType: null,
      profiles:[]
    }
  }


  setUser = user => this.setState({ user })
  setProfiles = () => {
    fetch('http://localhost:4741/show-all-profiles')
      .then((response) => {return response.json()})
      .then((data) => {this.setState({ profiles: data.profiles}) })
  }

  clearUser = () => this.setState({ user: null })

  flash = (message, type) => {
    this.setState({ flashMessage: message, flashType: type })

    clearTimeout(this.messageTimeout)

    this.messageTimeout = setTimeout(() => this.setState({flashMessage: null
    }), 2000)
  }

  render () {
    const { flashMessage, flashType, user } = this.state
    let homeJsx
    if (this.state.profiles && this.state.profiles.length > 0) {
      homeJsx = <Home profiles={this.state.profiles} user={user} />
    }
    return (
      <React.Fragment>
        <Header user={user} />
        {flashMessage && <h3 className={flashType}>{flashMessage}</h3>}

        <main className="container">
          <Route path='/sign-up' render={() => (
            <SignUp flash={this.flash} setUser={this.setUser} />
          )} />
          <Route path='/sign-in' render={() => (
            <SignIn setProfiles= {this.setProfiles} flash={this.flash} setUser={this.setUser} />
          )} />
          <AuthenticatedRoute user={user} path='/sign-out' render={() => (
            <SignOut flash={this.flash} clearUser={this.clearUser} user={user} />
          )} />
          <AuthenticatedRoute user={user} path='/change-password' render={() => (
            <ChangePassword flash={this.flash} user={user} />
          )} />
          <AuthenticatedRoute user={user} path='/create-profile' render={() => (
            <ProfileCreate flash={this.flash} user={user} editable={true} profileCrud={'create'}/>
          )} />
          {homeJsx}
        </main>
      </React.Fragment>
    )
  }
}

export default App



